html,body{
  height: 100%;
}

#wrapper{
  .content{
    &.content-white{
      background-color:@clr-white;
      color:@text-inlight;
    }
    &.content-yellow{
      background-color:@clr-yellow;
      color:@text-inlight;
    }
    &.content-blue{
      background-color:@clr-blue;
      color:@text-indark;
    }
    &.content-grey{
      background-color:@clr-grey-light;
      color:@text-inlight;
    }
    &.content-dark{
      background-color:@clr-grey-dark;
      color:@text-indark;
    }
    &.content-parallax{
      background-position:center;
      background-size:cover;
      background-attachment:fixed;
      background-repeat:no-repeat;
      background-image:url('../img/parrallax-1.jpg');
      &.content-parallax-lg{
        >.overlay-parallax{
          min-height:250px;
        }
      }
      >.overlay-parallax{
        &.overlay-parallax-dark{
          background-color:fade(@clr-black, 25%);
          color:@text-indark;
        }
        &.overlay-parallax-light{
          background-color:fade(@clr-white, 25%);
          color:@text-inlight;
        }
        &.overlay-parallax-blue{
          background-color:fade(@clr-blue, 25%);
          color:@text-indark;
        }
        &.overlay-parallax-blue-dark{
          background-color:fade(@clr-blue, 90%);
          color:@text-indark;
        }
        &.overlay-parallax-yellow{
          background-color:fade(@clr-yellow, 25%);
          color:@text-inlight;
        }
        &.overlay-parallax-yellow-dark{
          background-color:fade(@clr-yellow, 90%);
          color:@text-inlight;
        }
      }
    }
  }
  
  .title-content{
    margin-top:0;
    margin-bottom:0;
    >.title-secondary{
      display:block;
      margin-top:@padding-medium;
    }
    &.title-underline{
      &:after{
        content:"";
        background-color:@text-inlight;
        width:35px;
        height:6px;
        display:block;
        border-radius:@border-radius-small;
        margin:@padding-default 0;
      }
      &.title-underline-invert{
        &:after{
          background-color:@text-indark;
        }
      }
      &.text-center{
        &:after{
          margin:@padding-default auto;
        }
      }
      &.text-right{
        &:after{
          margin:@padding-default 0;
          margin-left:auto;
        }
      }
      @media(min-width:@grid-float-breakpoint){
        &.text-left-md{
          &:after{
            margin:@padding-default 0 !important;
          }
        }
        &.text-center-md{
          &:after{
            margin:@padding-default auto !important;
          }
        }
        &.text-right-md{
          &:after{
            margin:@padding-default 0 !important;
            margin-left:auto !important;
          }
        }
      }
      @media(max-width:@grid-float-breakpoint-max){
        &.text-center-sm{
          &:after{
            margin:@padding-default auto !important;
          }
        }
        &.text-left-sm{
          &:after{
            margin:@padding-default 0 !important;
          }
        }
        &.text-right-sm{
          &:after{
            margin:@padding-default 0 !important;
            margin-left:auto !important;
          }
        }
      }
      &.font-blue{
        &:after{
          background-color:@clr-blue;
        }
        &.title-underline-invert{
          &:after{
            background-color:@clr-yellow;
          }
        }
      }
      &.font-yellow{
        &:after{
          background-color:@clr-yellow;
        }
        &.title-underline-invert{
          &:after{
            background-color:@clr-blue;
          }
        }
      }
      &.font-black{
        &:after{
          background-color:@clr-black;
        }
        &.title-underline-invert{
          &:after{
            background-color:@clr-yellow;
          }
        }
      }
      &.font-blue-light{
        &:after{
          background-color:@clr-blue-light;
        }
        &.title-underline-invert{
          &:after{
            background-color:@clr-blue;
          }
        }
      }
    }
  }
  
  .info-box{
    position:relative;
    overflow:hidden;
    &.info-box-style-1{
      >.info-box-img{
        >img{
          width:100%;
        }
      }
      >.info-box-text{
        background-color:fade(@clr-blue,90%);
        color:@text-indark;
        position:absolute;
        bottom:0;
        top:0;
        @media(min-width:@grid-float-breakpoint){
          top:75%;
        }
        padding-left:@padding-default;
        padding-right:@padding-default;
        left:0;
        height:100%;
        width:100%;
        overflow:auto;
        .transition(.5s ease all);
        >.title{
          .transition(.3s ease all);
          >.icon{
            font-size: 40px;
            display: table-cell;
            vertical-align: middle;
            padding: 0 12px 0 0;
            >span{
              width:50px;
            }
          }
          >.title-text{
            display: table-cell;
            vertical-align: middle;
            padding-top: 0;
            margin: 0;
            font-weight: 700;
          }
        }
        >.text-showup{
          padding-left: 62px;
          .info-box-link{
            color:@clr-yellow;
            .transition(.3s color ease);
            &:hover,&:focus{
              text-decoration:none;
              color:@clr-white;
            }
          }
        }
      }
      &:hover,&:focus{
        @media(min-width:@grid-float-breakpoint){
          >.info-box-text{
            top:0;
            >.title{
              margin-top:(@padding-default * 2);
            }
          }
        }
      }
    }
  }
  
  .vc-box{
    position:relative;
    width:100%;
    display:block;
    padding:(@padding-default * 2);
    min-height:150px;
    @media(max-width:@grid-float-breakpoint-max){
      padding:@padding-default 0;
    }
    .vc-icon{
      font-size:22px;
      position:relative;
      width:78px;
      float: left;
      margin: 0;
      text-align: center;
      vertical-align: bottom;
      &:after,&:before{
        content:"";
        display: block;
        margin:0 auto;
        width: 6px;
        background: #fde428;
        height: 0;
        .transition(all 0.3s ease);
      }
    }
    .vc-text{
      position:relative;
      overflow:hidden;
    }
    &:hover,&:focus{
      background-color:@clr-white;
      .vc-icon{
        &:after,&:before{
          height:15px;
        }
      }
    }
  }
  
  .parallax-content{
    .icon-parallax{
      font-size:@font-size-h2;
      &.icon-parallax-lg{
        font-size:64px;
      }
    }
    .title-parallax{
      &.title-parallax-lg{
        font-size:(@font-size-h1 + 10);
      }
      .block{
        display:block;
      }
    }
  }
  
  .box-count-to{
    .count-to{
      &.count-to-lg{
        font-size:(@font-size-h1 + 10);
        @media(max-width:@grid-float-breakpoint-max){
          font-size:@font-size-h2;
        }
      }
    }
  }
  .box-content{
    .box-link{
      display:block;
      &:focus,&:hover{
        text-decoration:none;
      }
    }
    &.box-style-news{
      display:block;
      position:relative;
      width:100%;
      margin-top:(@padding-default * 2);
      .box-content-img{
        position:relative;
        .box-link{
          position:relative;
          img{
            width:100%;
          }
          &:after,&:before{
            opacity:0;
            position:absolute;
          }
          &:before{
            content:"";
            top:0;
            left:0;
            background-color:fade(@clr-black,50%);
            width:100%;
            height:100%;
            .transition(opacity .3s ease);
          }
          &:after{
            content:"\f067";
            top:50%;
            left:50%;
            .translate(-50%,-50%);
            margin:auto;
            color:@text-indark;
            font-size:@font-size-h1;
            font-family:FontAwesome;
            .transition(opacity .3s linear);
          }
          &:hover,&:focus{
            &:after,&:before{
              opacity:1;
            }
          }
        }
      }
      .box-content-text{
        position:relative;
        .box-link{
          .box-title{
            &:before{
              content:"";
              height:6px;
              width: 47px;
              display:block;
              border-radius:@border-radius-small;
              background-color:@clr-yellow;
              margin-bottom:@padding-default;
            }
          }
        }
      }
    }
    &.box-style-testimonial{
      display:block;
      position:relative;
      width:100%;
      .box-content-img{
        position:relative;
        .box-link{
          position:relative;
          img{
            width:100%;
          }
          &:after,&:before{
            opacity:0;
            position:absolute;
          }
          &:before{
            content:"";
            top:0;
            left:0;
            background-color:fade(@clr-black,50%);
            width:100%;
            height:100%;
            .transition(opacity .3s ease);
          }
          &:after{
            content:"\f067";
            top:50%;
            left:50%;
            .translate(-50%,-50%);
            margin:auto;
            color:@text-indark;
            font-size:@font-size-h1;
            font-family:FontAwesome;
            .transition(opacity .3s linear);
          }
          &:hover,&:focus{
            &:after,&:before{
              opacity:1;
            }
          }
        }
      }
      .box-content-text{
        position:relative;
      }
    }
    &.box-style-address{
      padding-right:(@padding-default * 2);
      @media(min-width:@grid-float-breakpoint){
        border-right:1px solid rgba(255, 255, 255, 0.25);
      }
      .box-icon{
        float: left;
        width: 16px;
        line-height: 20px;
        font-size: 16px;
        margin-right: 14px;
        text-align: center;
      }
      .box-text{
        overflow: hidden;
        font-size: 13px;
        line-height: 20px;
        a{
          color:@clr-blue-other;
          &:hover,&:focus{
            color:@clr-blue-other;
          }
        }
      }
    }
  }
  
  .link-img-grayscale{
    display:block;
    width:100%;
    position:relative;
    padding-top:@padding-default;
    padding-bottom:@padding-default;
    >img{
      .transition(.5s ease all);
      opacity:0.8;
      .grayscale(1);
    }
    &:hover,&:focus{
      >img{
        opacity:1;
        .grayscale(0);
      }
    }
  }

  .single-page{
    padding:(@padding-default * 2.5) 0;
    @media(min-width:@grid-float-breakpoint){
      padding-top:(@header-navbar-big + (@padding-default * 2.5));
    }
  }
  
  .post-content{
    line-height:@line-height-post;
    figure,.media-assets{
      float:left;
      margin-right:@padding-default;
      margin-bottom:@padding-default;
      &.figure-right,&.right,&.media-assets-right{
        float:right;
        margin-right:0;
        margin-left:@padding-default;
      }
      &.figure-full,&.full,&.block,&.media-assets-full{
        text-align:center;
        float:none;
        margin:@padding-default 0;
        width:100%;
        display:block;
      }
    }
  }
  
  .list-history{
    .history-story{
      position:relative;
      &:after{
        content: "";
        height: 10px;
        width: 10px;
        background: @clr-yellow;
        position: absolute;
        top: 10px;
        left: -17.5px;
        border-radius: 50%;
      }
      &:before{
        content: "";
        height: 100%;
        width: 2px;
        background: #e6e6e6;
        position: absolute;
        left: -15px;
        top:10px;
      }
    }
  }
  
  .work-grid{
    margin-bottom:(@padding-default * 2);
    position:relative;
    .transition(.5s ease all);
    .work-grid-img{
      position:relative;
      display:block;
      width:100%;
      >img{
        width:100%;
      }
    }
    .work-grid-text{
      background-color:fade(@clr-blue,75%);
      position:absolute;
      top:0;
      left:0;
      width:100%;
      height:100%;
      color:@text-indark;
      padding:(@padding-default * 2);
      @media(min-width:@grid-float-breakpoint){
        opacity:0;
        visibility:hidden;
        .transition(.5s ease all);
      }
      @media(max-width:@grid-float-breakpoint-max){
        padding:@padding-default;
      }
      >.work-grid-title{
        bottom:74px;
        font-weight: 600;
        font-size: 22px;
        color: @text-indark;
        letter-spacing: -0.04em;
        line-height: 24px;
        @media(min-width:@grid-float-breakpoint){
          opacity:0;
          visibility:hidden;
          .translate(0, -50%);
          .transition(.5s ease all);
        }
        &:before{
          content:"";
          display: block;
          width: 47px;
          height: 5px;
          background: @clr-yellow;
          margin: 0 0 19px;
        }
      }
      >.work-grid-desc{
        bottom: 20px;
        left: 30px;
        right: 30px;
        color: #fff;
        font-size: 13px;
        @media(min-width:@grid-float-breakpoint){
          opacity:0;
          visibility:hidden;
          .transition(.5s ease all);
          .translate(0, 50%);
        }
      }
    }
    
    .work-grid-link{
      position:absolute;
      top:0;
      left:0;
      width:100%;
      height:100%;
      @media(min-width:@grid-float-breakpoint){
        opacity:0;
        visibility:hidden;
        display:none;
      }
    }
    @media(min-width:@grid-float-breakpoint){
      &:hover,&:focus{
        .work-grid-text{
          opacity:1;
          visibility:visible;
          >.work-grid-title{
            opacity:1;
            visibility:visible;
            .translate(0, 0);
          }
          >.work-grid-desc{
            opacity:1;
            visibility:visible;
            .translate(0, 0);
          }
        }
        .work-grid-link{
          opacity:1;
          visibility:visible;
          display:block;
        }
      }
    }
  }
  
}
