aside#sidebar{
  >.sidebar-item{
    margin-bottom:(@padding-default * 2.5);
    @media(max-width:@grid-float-breakpoint-max){
      margin-bottom:(@padding-default * 1.5);
    }
    .list-sidebar-menu{
      >li{
        >a{
          display:block;
          font-weight:600;
          border-bottom:1px solid #fff;
          background-color:@clr-grey-light;
          color:@clr-blue;
          padding:@padding-default;
          border-left:5px solid transparent;
          .transition(.5s ease all);
          &:hover,&:focus{
            text-decoration:none;
            border-left-color:@clr-blue-light;
          }
        }
        &.active{
          >a{
            &,&:hover,&:focus{
              border-left-color:@clr-blue-light;
            }
          }
        }
      }
    }
    
    .sidebar-box{
      padding:(@padding-default * 2);
      &.sidebar-box-yellow{
        background-color:@clr-yellow;
        color:@text-inlight;
      }
      &.sidebar-box-blue{
        background-color:@clr-blue;
        color:@text-indark;
      }
    }
    
    .people-quote{
      >.quote-item{
        background-color:@clr-white;
        border:3px solid @clr-grey-light;
        padding:(@padding-default * 2);
        position:relative;
        margin-bottom:30px;
        &:after{
          content:"";
          position:absolute;
          bottom:-30px;
          left: (@padding-default * 4 + 2);
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 30px 30px 0 0;
          border-color: @clr-grey-light transparent transparent transparent;
          line-height: 0px;
          _border-color: @clr-grey-light #000000 #000000 #000000;
          _filter: progid:DXImageTransform.Microsoft.Chroma(color='#000000');
        }
        &:before{
          content: "\f10d";
          font-family: FontAwesome;
          speak: none;
          font-style: normal;
          font-weight: normal;
          font-variant: normal;
          text-transform: none;
          line-height: 1;
          -webkit-font-smoothing: antialiased;
          -moz-osx-font-smoothing: grayscale;
          font-size: 48px;
          color: @clr-yellow;
          position: absolute;
          right: 0;
          bottom: 0;
          height: 40px;
          overflow: hidden;
        }
      }
      >.quote-person{
        >.person-img{
          max-width: 50px;
          display: table-cell;
          vertical-align: middle;
          >img{
            max-width: 100%;
            height: auto;
            overflow: hidden;
          }
        }
        >.person-detail{
          display: table-cell;
          vertical-align: middle;
          padding: 0 0 0 @padding-default;
          >.person-name{
            font-size: 13px;
            font-weight: 600;
          }
        }
      }
    }
    
  }
}
