footer#footer{
  .box-shadow(inset 0 2px 5px 0px rgba(0, 0, 0, 0.39));
  background-color:@clr-blue;
  color:@text-indark;
  .muted{
    color:@text-indark;
    opacity:.5;
  }
  a{
    color:@clr-blue-other;
    &:hover,&:focus{
      color:@clr-blue-other;
    }
    &.link-block{
      display:block;
    }
  }
  .footer-top{
    .img-footer{
      height:45px;
    }
    .form{
      .form-control{
        border-radius:0;
        .form-control-focus(@clr-yellow);
      }
      .input-group-btn{
        .btn{
          border-radius:0;
        }
      }
    }
    .title-footer{
      border-bottom:1px solid rgba(204, 204, 204, 0.5);
      &.title-no-underline{
        border-bottom:none;
      }
    }
    .list-footer-1{
      >li{
        a{
          color:@text-indark;
          .transition(.5s ease color);
          &:hover,&:focus{
            color:@clr-yellow;
            text-decoration:none;
          }
        }
        .fa{
          margin-right:@padding-small;
        }
      }
    }
    .list-footer-2{
      >li{
        width:50%;
        float:left;
        >a{
          display:block;
          padding-right:@padding-default;
          margin-bottom:@padding-default;
        }
      }
    }
  }
  .footer-bottom{
    color:@text-indark;
    a{
      opacity:1;
    }
    .container{
      border-top:1px solid rgba(204, 204, 204, 0.5);
      .copyright{
        line-height:37px;
      }
      .list-socmed{
        >li{
          >a{
            color:@text-indark;
            .transition(.5s ease color);
            .fa-inverse{
              .transition(.5s ease color);
              color:@clr-blue;
            }
            &:hover,&:focus{
              color:@clr-blue-other;
              .fa-inverse{
               color:@clr-white!important; 
              }
            }
            &.facebook{
              &:hover,&:focus{
                color:#3B5998;
              }
            }
            &.twitter{
              &:hover,&:focus{
                color:#6CADDE;
              }
            }
            &.google-plus{
              &:hover,&:focus{
                color:#F44336;
              }
            }
            &.linkedin{
              &:hover,&:focus{
                color:#007AB6;
              }
            }
            &.instagram{
              &:hover,&:focus{
                color:#A87E62;
              }
            }
            &.github{
              &:hover,&:focus{
                color:#000;
              }
            }
          }
        }
      }
    }
  }
}
